.backdrop{
    position: fixed;
    top: 0px;
    bottom: 0px;
    left: 0px;
    right: 0px;
    background: rgba(0, 0, 0, 0.5);
    display: flex;
    backdrop-filter: blur(2px);
    z-index: 200;
}

@media screen and (min-width:570px) {
    .backdrop {
        display: none;
    }
}